import { SIGN_IN, USER_INFO, SIGN_OUT } from "../actions/types";

const initialState = {
	user: {
		isLoading: true,
		hasError: false,
		isLoggedIn: false,
		data: [],
	},
	signin: {
		isLoading: false,
		hasError: false,
		data: [],
	},
};

export default function (state = initialState, action) {
	switch (action.type) {
		case SIGN_IN:
			return {
				...state,
				signin: action.payload,
			};
		case USER_INFO:
			return {
				...state,
				user: action.payload,
			};
		case SIGN_OUT:
			return {
				...state,
				user: action.payload,
			};
		default:
			return state;
	}
}
